<script>
  /**
   * üó∫Ô∏è MapTab.vue - D3.js ‰∏ñÁïåÂú∞ÂúñÁµÑ‰ª∂ (D3.js World Map Component)
   *
   * ‰ΩøÁî® D3.js Áπ™Ë£Ω‰∏ñÁïåÂú∞ÂúñÔºåÂ∞àÁÇ∫‰∏ñÁïåÂüéÂ∏ÇÂú∞ÂúñÂ±ïÁ§∫Ë®≠Ë®à„ÄÇ
   * ‰∏ªË¶ÅÂäüËÉΩÔºö
   * - ‰ΩøÁî® D3.js È°ØÁ§∫‰∏ñÁïåÂú∞Âúñ
   * - Êèê‰æõÂüéÂ∏ÇÂ∞éËà™ÂäüËÉΩ
   * - ÊîØÊè¥Â§öÁ®ÆÊäïÂΩ±ÊñπÂºè
   * - ÈüøÊáâÂºèË®≠Ë®à
   *
   * ÊäÄË°ìÊû∂ÊßãÔºö
   * - Vue 3 Composition API
   * - D3.js Âú∞ÂúñÁπ™Ë£Ω
   * - Pinia ÁãÄÊÖãÁÆ°ÁêÜ
   * - Bootstrap 5 Ê®£Âºè
   */

  import { ref, onMounted, onUnmounted, watch, nextTick, computed } from 'vue';
  import * as d3 from 'd3';
  import {
    geoAiry,
    geoAitoff,
    geoArmadillo,
    geoAugust,
    geoBaker,
    geoBerghaus,
    geoBertin1953,
    geoBoggs,
    geoBonne,
    geoBottomley,
    geoBromley,
    geoChamberlin,
    geoChamberlinAfrica,
    geoCollignon,
    geoCraig,
    geoCraster,
    geoCylindricalEqualArea,
    geoCylindricalStereographic,
    geoEckert1,
    geoEckert2,
    geoEckert3,
    geoEckert4,
    geoEckert5,
    geoEckert6,
    geoEisenlohr,
    geoFahey,
    geoFoucaut,
    geoFoucautSinusoidal,
    geoGilbert,
    geoGingery,
    geoGinzburg4,
    geoGinzburg5,
    geoGinzburg6,
    geoGinzburg8,
    geoGinzburg9,
    geoGringorten,
    geoGringortenQuincuncial,
    geoGuyou,
    geoHammer,
    geoHammerRetroazimuthal,
    geoHealpix,
    geoHill,
    geoHomolosine,
    geoHufnagel,
    geoHyperelliptical,
    geoInterruptedBoggs,
    geoInterruptedHomolosine,
    geoInterruptedMollweide,
    geoInterruptedMollweideHemispheres,
    geoInterruptedQuarticAuthalic,
    geoInterruptedSinuMollweide,
    geoInterruptedSinusoidal,
    geoKavrayskiy7,
    geoLagrange,
    geoLarrivee,
    geoLaskowski,
    geoLittrow,
    geoLoximuthal,
    geoMiller,
    geoMollweide,
    geoMtFlatPolarParabolic,
    geoMtFlatPolarQuartic,
    geoMtFlatPolarSinusoidal,
    geoNaturalEarth2,
    geoNellHammer,
    geoNicolosi,
    geoPatterson,
    geoPeirceQuincuncial,
    geoPolyconic,
    geoPolyhedralButterfly,
    geoPolyhedralCollignon,
    geoPolyhedralWaterman,
    geoRectangularPolyconic,
    geoRobinson,
    geoSatellite,
    geoSinuMollweide,
    geoSinusoidal,
    geoTimes,
    geoTwoPointAzimuthal,
    geoTwoPointEquidistant,
    geoVanDerGrinten,
    geoVanDerGrinten2,
    geoVanDerGrinten3,
    geoVanDerGrinten4,
    geoWagner4,
    geoWagner6,
    geoWagner7,
    geoWiechel,
    geoWinkel3,
  } from 'd3-geo-projection';
  import { useDataStore } from '@/stores/dataStore.js';

  const TAIWAN_CENTER = [120.9820246, 23.9738747];

  export default {
    name: 'MapTab',
    props: {
      currentProjection: { type: String, default: 'Azimuthal Equal Area' },
    },
    emits: ['map-ready'],
    setup(props, { emit }) {
      // üì¶ Â≠òÂÑ≤ÂØ¶‰æã
      const dataStore = useDataStore();

      // üó∫Ô∏è Âú∞ÂúñÁõ∏ÈóúËÆäÊï∏
      const mapContainer = ref(null);
      const svgElement = ref(null);
      let svg = null;
      let projection = null;
      let path = null;
      let zoom = null;
      let g = null;
      let gBorder = null; // ÈÇäÊ°ÜÁµÑÔºà‰∏çÂèóË£ÅÂâ™ÂΩ±ÈüøÔºâ
      let clipPathId = null;

      // üéõÔ∏è Âú∞ÂúñÊéßÂà∂ÁãÄÊÖã
      const isMapReady = ref(false);
      const mapContainerId = ref(`d3-map-${Math.random().toString(36).substr(2, 9)}`);

      // ‰∏ñÁïåÂú∞ÂúñÊï∏Êìö
      const worldData = ref(null);

      // Áï∂ÂâçÊäïÂΩ±È°ûÂûãÂíåÁ∏ÆÊîæÊØî‰æã
      const currentProjectionType = ref('AzimuthalEquidistant');
      const currentScale = ref(80);
      const centerPresets = {
        origin: [0, 0],
        taiwan: TAIWAN_CENTER,
        lon120: [120, 0],
      };
      const currentCenterMode = ref('origin');
      const currentCenterCoords = ref(centerPresets.origin);
      const currentViewMode = ref('world');

      // ConicConformal ÊäïÂΩ±ÁöÑÊîæÂ§ßÂÄçÁéá
      const conicConformalScale = ref(5000);

      // ÂúìÂúàÁèæÂú®‰ΩøÁî® D3.js Áπ™Ë£ΩÔºå‰∏çÈúÄË¶ÅÂ§ßÂ∞èË®àÁÆóÂáΩÊï∏

      // üìä Ë®àÁÆóÂ±¨ÊÄßÔºöÊ™¢Êü•ÊòØÂê¶Êúâ‰ªª‰ΩïÂúñÂ±§ÂèØË¶ã
      const isAnyLayerVisible = computed(() => dataStore.getAllLayers().length > 0);

      /**
       * üó∫Ô∏è ÂâµÂª∫ÊäïÂΩ±
       * Ê†πÊìöÊäïÂΩ±È°ûÂûãÂâµÂª∫Â∞çÊáâÁöÑ D3.js ÊäïÂΩ±Ôºå‰∏¶Ëá™ÂãïÈÅ©ÊáâÁâàÈù¢Â§ßÂ∞è
       */
      const createProjection = (type, width, height) => {
        let proj;
        const [centerLon, centerLat] = currentCenterCoords.value;
        const rotation = [-centerLon, -centerLat, 0];

        switch (type) {
          case 'AzimuthalEqualArea':
            proj = d3.geoAzimuthalEqualArea();
            break;
          case 'AzimuthalEquidistant':
            proj = d3.geoAzimuthalEquidistant();
            break;
          case 'Gnomonic':
            proj = d3.geoGnomonic();
            break;
          case 'Orthographic':
            proj = d3.geoOrthographic();
            break;
          case 'Stereographic':
            // Stereographic ÊäïÂΩ±ÔºöÊù±Á∂ì120Â∫¶„ÄÅÂåóÁ∑Ø0Â∫¶‰∏≠ÂøÉ
            proj = d3.geoStereographic();
            break;
          case 'Albers':
            proj = d3.geoAlbers().parallels([20, 60]);
            break;
          case 'ConicConformal':
            // Conic Conformal ÊäïÂΩ±ÔºöÊ®ôÊ∫ñÂúìÈåêÊäïÂΩ±Ôºå‰∏≠ÂøÉÈªûË®≠ÁÇ∫Á∂ìÁ∑ØÂ∫¶ 0,0
            proj = d3
              .geoConicConformal()
              .parallels([20, 60]) // Ê®ôÊ∫ñÁ∑ØÁ∑öÔºöÂåóÁ∑Ø20¬∞ Âíå 60¬∞
              .rotate([0, 0]) // ÊóãËΩâÔºöËÆìÁ∂ìÂ∫¶0Â∫¶ÊàêÁÇ∫‰∏≠ÂøÉÁ∑ö
              .center([0, 0]); // ‰∏≠ÂøÉÈªû [Á∂ìÂ∫¶, Á∑ØÂ∫¶] (0¬∞Ôºå0¬∞)
            break;
          case 'ConicEqualArea':
            proj = d3.geoConicEqualArea().parallels([20, 60]);
            break;
          case 'ConicEquidistant':
            proj = d3.geoConicEquidistant().parallels([20, 60]);
            break;
          case 'Equirectangular':
            proj = d3.geoEquirectangular();
            break;
          case 'Mercator':
            proj = d3.geoMercator();
            break;
          case 'TransverseMercator':
            proj = d3.geoTransverseMercator();
            break;
          case 'NaturalEarth':
            proj = d3.geoNaturalEarth1();
            break;
          case 'Airy':
            proj = geoAiry();
            break;
          case 'Aitoff':
            proj = geoAitoff();
            break;
          case 'Armadillo':
            proj = geoArmadillo();
            break;
          case 'August':
            proj = geoAugust();
            break;
          case 'Baker':
            proj = geoBaker();
            break;
          case 'Berghaus':
            proj = geoBerghaus();
            break;
          case 'Bertin1953':
            proj = geoBertin1953();
            break;
          case 'Boggs':
            proj = geoBoggs();
            break;
          case 'Bonne':
            proj = geoBonne();
            break;
          case 'Bottomley':
            proj = geoBottomley();
            break;
          case 'Bromley':
            proj = geoBromley();
            break;
          case 'Chamberlin':
            // Chamberlin ÈúÄË¶Å3‰∏™ÁÇπÂèÇÊï∞Ôºå‰ΩøÁî®Âè∞ÊπæÂíåÂë®ËæπÂú∞Âå∫
            proj = geoChamberlin([120.98, 23.97], [121.5, 25.0], [120.0, 22.0]);
            break;
          case 'ChamberlinAfrica':
            proj = geoChamberlinAfrica();
            break;
          case 'Collignon':
            proj = geoCollignon();
            break;
          case 'Craig':
            proj = geoCraig();
            break;
          case 'Craster':
            proj = geoCraster();
            break;
          case 'CylindricalEqualArea':
            proj = geoCylindricalEqualArea();
            break;
          case 'CylindricalStereographic':
            proj = geoCylindricalStereographic();
            break;
          case 'Eckert1':
            proj = geoEckert1();
            break;
          case 'Eckert2':
            proj = geoEckert2();
            break;
          case 'Eckert3':
            proj = geoEckert3();
            break;
          case 'Eckert4':
            proj = geoEckert4();
            break;
          case 'Eckert5':
            proj = geoEckert5();
            break;
          case 'Eckert6':
            proj = geoEckert6();
            break;
          case 'Eisenlohr':
            proj = geoEisenlohr();
            break;
          case 'Fahey':
            proj = geoFahey();
            break;
          case 'Foucaut':
            proj = geoFoucaut();
            break;
          case 'FoucautSinusoidal':
            proj = geoFoucautSinusoidal();
            break;
          case 'Gilbert':
            proj = geoGilbert();
            break;
          case 'Gingery':
            proj = geoGingery();
            break;
          case 'Ginzburg4':
            proj = geoGinzburg4();
            break;
          case 'Ginzburg5':
            proj = geoGinzburg5();
            break;
          case 'Ginzburg6':
            proj = geoGinzburg6();
            break;
          case 'Ginzburg8':
            proj = geoGinzburg8();
            break;
          case 'Ginzburg9':
            proj = geoGinzburg9();
            break;
          case 'Gringorten':
            proj = geoGringorten();
            break;
          case 'GringortenQuincuncial':
            proj = geoGringortenQuincuncial();
            break;
          case 'Guyou':
            proj = geoGuyou();
            break;
          case 'HammerRetroazimuthal':
            proj = geoHammerRetroazimuthal();
            break;
          case 'Healpix':
            proj = geoHealpix();
            break;
          case 'Hill':
            proj = geoHill();
            break;
          case 'Homolosine':
            proj = geoHomolosine();
            break;
          case 'Hufnagel':
            proj = geoHufnagel();
            break;
          case 'Hyperelliptical':
            proj = geoHyperelliptical();
            break;
          case 'InterruptedHomolosine':
            proj = geoInterruptedHomolosine();
            break;
          case 'InterruptedMollweide':
            proj = geoInterruptedMollweide();
            break;
          case 'InterruptedMollweideHemispheres':
            proj = geoInterruptedMollweideHemispheres();
            break;
          case 'InterruptedQuarticAuthalic':
            proj = geoInterruptedQuarticAuthalic();
            break;
          case 'InterruptedSinuMollweide':
            proj = geoInterruptedSinuMollweide();
            break;
          case 'Kavrayskiy7':
            proj = geoKavrayskiy7();
            break;
          case 'Lagrange':
            proj = geoLagrange();
            break;
          case 'Larrivee':
            proj = geoLarrivee();
            break;
          case 'Laskowski':
            proj = geoLaskowski();
            break;
          case 'Littrow':
            proj = geoLittrow();
            break;
          case 'Loximuthal':
            proj = geoLoximuthal();
            break;
          case 'Miller':
            proj = geoMiller();
            break;
          case 'MtFlatPolarParabolic':
            proj = geoMtFlatPolarParabolic();
            break;
          case 'MtFlatPolarQuartic':
            proj = geoMtFlatPolarQuartic();
            break;
          case 'MtFlatPolarSinusoidal':
            proj = geoMtFlatPolarSinusoidal();
            break;
          case 'NaturalEarth2':
            proj = geoNaturalEarth2();
            break;
          case 'NellHammer':
            proj = geoNellHammer();
            break;
          case 'Nicolosi':
            proj = geoNicolosi();
            break;
          case 'Patterson':
            proj = geoPatterson();
            break;
          case 'Polyconic':
            proj = geoPolyconic();
            break;
          case 'PolyhedralCollignon':
            proj = geoPolyhedralCollignon();
            break;
          case 'PolyhedralWaterman':
            proj = geoPolyhedralWaterman();
            break;
          case 'RectangularPolyconic':
            proj = geoRectangularPolyconic();
            break;
          case 'InterruptedSinusoidal':
            proj = geoInterruptedSinusoidal();
            break;
          case 'InterruptedBoggs':
            proj = geoInterruptedBoggs();
            break;
          case 'PolyhedralButterfly':
            proj = geoPolyhedralButterfly();
            break;
          case 'PeirceQuincuncial':
            proj = geoPeirceQuincuncial();
            break;
          case 'Hammer':
            proj = geoHammer();
            break;
          case 'Mollweide':
            proj = geoMollweide();
            break;
          case 'Robinson':
            proj = geoRobinson();
            break;
          case 'Sinusoidal':
            proj = geoSinusoidal();
            break;
          case 'Satellite':
            proj = geoSatellite();
            break;
          case 'SinuMollweide':
            proj = geoSinuMollweide();
            break;
          case 'Times':
            proj = geoTimes();
            break;
          case 'TwoPointAzimuthal':
            // TwoPoint ÈúÄË¶Å2‰∏™ÁÇπÂèÇÊï∞Ôºå‰ΩøÁî®Âè∞ÊπæÂíå‰∏ú‰∫¨
            proj = geoTwoPointAzimuthal([120.98, 23.97], [139.69, 35.68]);
            break;
          case 'TwoPointEquidistant':
            // TwoPoint ÈúÄË¶Å2‰∏™ÁÇπÂèÇÊï∞Ôºå‰ΩøÁî®Âè∞ÊπæÂíå‰∏ú‰∫¨
            proj = geoTwoPointEquidistant([120.98, 23.97], [139.69, 35.68]);
            break;
          case 'VanDerGrinten':
            proj = geoVanDerGrinten();
            break;
          case 'VanDerGrinten2':
            proj = geoVanDerGrinten2();
            break;
          case 'VanDerGrinten3':
            proj = geoVanDerGrinten3();
            break;
          case 'VanDerGrinten4':
            proj = geoVanDerGrinten4();
            break;
          case 'Wagner4':
            proj = geoWagner4();
            break;
          case 'Wagner6':
            proj = geoWagner6();
            break;
          case 'Wagner7':
            proj = geoWagner7();
            break;
          case 'Wiechel':
            proj = geoWiechel();
            break;
          case 'Winkel3':
            proj = geoWinkel3();
            break;
          default:
            proj = d3.geoAzimuthalEquidistant();
        }

        // Á¢∫‰øùÊäïÂΩ±Â∞çË±°ÊúâÊïà
        if (!proj) {
          console.error('[MapTab] ÊäïÂΩ±ÂâµÂª∫Â§±Êïó:', type);
          proj = d3.geoAzimuthalEquidistant(); // ÈôçÁ¥öÂà∞È†êË®≠ÊäïÂΩ±
        }

        // ‰ΩøÁî®Âπæ‰ΩïÈÇäÁïåËá™ÂãïÈÅ©ÊáâË¶ñÁ™óÔºà‰øùÁïô 32px ÈÇäË∑ùÔºâ
        const padding = 32;
        const extent = [
          [padding, padding],
          [width - padding, height - padding],
        ];
        const fitTarget = getFitTarget();

        // ÈáùÂ∞ç‰∏çÂêåÊäïÂΩ±È°ûÂûãË®≠ÂÆö‰∏çÂêåÁöÑÊóãËΩâ
        if (proj.rotate) {
          // Ê†πÊìöÊâÄÈÅ∏‰∏≠ÂøÉË®≠ÂÆöÊäïÂΩ±ÊóãËΩâ
          try {
            proj.rotate(rotation);
          } catch (e) {
            console.warn('[MapTab] rotate Â§±Êïó:', type, e);
          }
        }

        const supportsCenterMethod = [
          'Albers',
          'ConicEqualArea',
          'ConicEquidistant',
          'AzimuthalEqualArea',
          'AzimuthalEquidistant',
          'Gnomonic',
          'Orthographic',
        ];

        try {
          if (proj.fitExtent) {
            if (type === 'ConicConformal') {
              proj.fitExtent(extent, fitTarget);
              const currentScale = proj.scale();
              proj.scale(currentScale * conicConformalScale.value);
            } else if (supportsCenterMethod.includes(type) && proj.center && currentViewMode.value === 'world') {
              proj.center([0, 0]).fitExtent(extent, fitTarget);
            } else {
              proj.fitExtent(extent, fitTarget);
            }
          } else if (proj.scale && proj.translate) {
            const scale = Math.min(width, height) / 2 - padding;
            proj.scale(scale).translate([width / 2, height / 2]);
          }
        } catch (e) {
          console.error('[MapTab] fitExtent Â§±Êïó:', type, e);
          try {
            const scale = Math.min(width, height) / 2 - padding;
            if (proj.scale && proj.translate) {
              proj.scale(scale).translate([width / 2, height / 2]);
            }
          } catch (e2) {
            console.error('[MapTab] ÈôçÁ¥öÁ≠ñÁï•‰πüÂ§±Êïó:', type, e2);
          }
        }

        return proj;
      };

      /**
       * üîß Ë®≠ÂÆö ConicConformal ÊîæÂ§ßÂÄçÁéá
       * Ë®≠ÂÆöÂúìÈåê‰øùËßíÊäïÂΩ±ÁöÑÊîæÂ§ßÂÄçÁéá
       */
      const setConicConformalScale = (scale) => {
        conicConformalScale.value = scale;
        // Â¶ÇÊûúÁï∂ÂâçÊòØ ConicConformal ÊäïÂΩ±ÔºåÁ´ãÂç≥Êõ¥Êñ∞
        if (currentProjectionType.value === 'ConicConformal') {
          changeProjection('ConicConformal', currentScale.value);
        }
      };

      /**
       * üîÑ ÂàáÊèõÊäïÂΩ±È°ûÂûã
       * ÂãïÊÖãÊõ¥Êñ∞Âú∞ÂúñÁöÑÊäïÂΩ±È°ûÂûã
       */
      const changeProjection = (type, scale) => {
        if (!svg || !mapContainer.value) return;

        currentProjectionType.value = type;
        currentScale.value = scale;

        const rect = mapContainer.value.getBoundingClientRect();
        const width = rect.width;
        const height = rect.height;

        projection = createProjection(type, width, height);
        path = d3.geoPath().projection(projection);

        // Êõ¥Êñ∞Ë£ÅÂâ™Ë∑ØÂæë
        if (clipPathId) {
          svg.select(`#${clipPathId} path`).datum({ type: 'Sphere' }).attr('d', path);
        }

        renderSphereBorder();
        renderCountries();
        renderGridLines();
        renderTaiwanGuides();

        console.log('[MapTab] ÊäïÂΩ±ÂàáÊèõÂÆåÊàêÔºåÈ°ûÂûã:', type, 'Á∏ÆÊîæ:', scale, 'Ê®°Âºè:', currentViewMode.value);
      };

      const setMapCenter = (mode) => {
        const preset = centerPresets[mode] || centerPresets.origin;
        currentCenterMode.value = mode;
        currentCenterCoords.value = preset;
        if (isMapReady.value) {
          changeProjection(currentProjectionType.value, currentScale.value);
        }
      };

      const setViewMode = (mode) => {
        const normalized = mode === 'taiwan' ? 'taiwan' : 'world';
        if (currentViewMode.value === normalized && isMapReady.value) {
          if (normalized === 'taiwan' && currentCenterMode.value !== 'taiwan') {
            setMapCenter('taiwan');
          }
          return;
        }

        const previousMode = currentViewMode.value;
        currentViewMode.value = normalized;

        if (normalized === 'taiwan' && currentCenterMode.value !== 'taiwan') {
          setMapCenter('taiwan');
          if (previousMode !== normalized) {
            return;
          }
        }

        if (isMapReady.value) {
          changeProjection(currentProjectionType.value, currentScale.value);
        }
      };

      /**
       * üì• ËºâÂÖ•‰∏ñÁïåÂú∞ÂúñÊï∏Êìö
       */
      const loadWorldData = async () => {
        try {
          // ‰ΩøÁî®Êú¨Âú∞ÁöÑ GeoJSON Ê™îÊ°à
          console.log('[MapTab] ÈñãÂßãËºâÂÖ• GeoJSON Êï∏Êìö...');
          const response = await fetch(
            `${process.env.BASE_URL}data/ne_110m_admin_0_countries.geojson`
          );

          if (!response.ok) {
            throw new Error(`HTTP ÈåØË™§! ÁãÄÊÖã: ${response.status}`);
          }

          const data = await response.json();
          worldData.value = data;
          console.log('[MapTab] ‰∏ñÁïåÂú∞ÂúñÊï∏ÊìöËºâÂÖ•ÊàêÂäüÔºåÁâπÂæµÊï∏Èáè:', data.features?.length);
          return true;
        } catch (error) {
          console.error('[MapTab] ‰∏ñÁïåÂú∞ÂúñÊï∏ÊìöËºâÂÖ•Â§±Êïó:', error);
          return false;
        }
      };

      /**
       * üèóÔ∏è ÂâµÂª∫Âú∞ÂúñÂØ¶‰æã
       * ÂàùÂßãÂåñ D3 Âú∞Âúñ‰∏¶Ë®≠ÂÆöÂü∫Êú¨ÈÖçÁΩÆ
       */
      const createMap = () => {
        if (!mapContainer.value) return false;

        const rect = mapContainer.value.getBoundingClientRect();
        if (rect.width === 0 || rect.height === 0) {
          console.warn('[MapTab] ÂÆπÂô®Â∞∫ÂØ∏ÁÇ∫Èõ∂ÔºåÂª∂ÈÅ≤ÂàùÂßãÂåñ');
          return false;
        }

        try {
          const width = rect.width;
          const height = rect.height;

          // ÂâµÂª∫ SVG ÂÖÉÁ¥†
          svg = d3
            .select(mapContainer.value)
            .append('svg')
            .attr('width', width)
            .attr('height', height)
            .style('background', '#cecece');

          svgElement.value = svg.node();

          // ÂâµÂª∫ÊäïÂΩ± - ‰ΩøÁî®ÂèÉÊï∏ÂåñÁöÑÊäïÂΩ±È°ûÂûã
          // ÊâÄÊúâÊäïÂΩ±‰ª•Á∂ìÁ∑ØÂ∫¶ 0,0 ÁÇ∫‰∏≠ÂøÉÔºåËá™ÂãïÈÅ©ÊáâÁâàÈù¢Â§ßÂ∞è
          projection = createProjection(currentProjectionType.value, width, height);

          // ÂâµÂª∫Ë∑ØÂæëÁîüÊàêÂô®
          path = d3.geoPath().projection(projection);

          // ÂâµÂª∫Ë£ÅÂâ™Ë∑ØÂæëÔºàÁî®ÊñºË£ÅÂâ™Ë∂ÖÂá∫ÊäïÂΩ±ÈÇäÁïåÁöÑÂÖßÂÆπÔºâ
          clipPathId = `clip-${Math.random().toString(36).substr(2, 9)}`;
          const defs = svg.append('defs');
          const clipPath = defs.append('clipPath').attr('id', clipPathId);
          clipPath.append('path').datum({ type: 'Sphere' }).attr('d', path);

          // ÂâµÂª∫ÂÆπÂô®ÁµÑÔºàÂèóË£ÅÂâ™ÂΩ±ÈüøÔºåÁî®ÊñºÂú∞ÂúñÂÖßÂÆπÔºâ
          g = svg.append('g').attr('clip-path', `url(#${clipPathId})`);

          // ÂâµÂª∫ÈÇäÊ°ÜÁµÑÔºà‰∏çÂèóË£ÅÂâ™ÂΩ±ÈüøÔºåÁî®ÊñºÈ°ØÁ§∫ÊäïÂΩ±ÈÇäÁïåÔºâ
          gBorder = svg.append('g').attr('class', 'border-group');

          // Ë®≠ÁΩÆÁ∏ÆÊîæË°åÁÇ∫ÔºàÁ¶ÅÁî®ÊâÄÊúâ‰∫íÂãïÔºâ
          zoom = d3
            .zoom()
            .scaleExtent([1, 1]) // Á¶ÅÁî®Á∏ÆÊîæ
            .on('zoom', null); // Á¶ÅÁî®Á∏ÆÊîæ‰∫ã‰ª∂

          svg.call(zoom).on('wheel.zoom', null).on('dblclick.zoom', null);

          isMapReady.value = true;

          // Â∞áÂú∞ÂúñÂØ¶‰æãÂíåÊñπÊ≥ï‰∏ÄËµ∑ÂÇ≥ÈÅû
          const mapInterface = {
            svg,
            projection,
            path,
            navigateToLocation: () => navigateToLocation(),
            changeProjection: (type, scale) => changeProjection(type, scale),
            setMapCenter: (mode) => setMapCenter(mode),
            setViewMode: (mode) => setViewMode(mode),
          };

          emit('map-ready', mapInterface);

          console.log('[MapTab] D3 Âú∞ÂúñÂâµÂª∫ÊàêÂäü');
          return true;
        } catch (error) {
          console.error('[MapTab] D3 Âú∞ÂúñÂâµÂª∫Â§±Êïó:', error);
          return false;
        }
      };

      /**
       * üåê ÁîüÊàêÁ∂ìÁ∑ØÁ∑öÁ∂≤Ê†ºÊï∏Êìö
       * ÁîüÊàêÊØè30Â∫¶ÁöÑÁ∂ìÁ∑öÂíåÁ∑ØÁ∑öÔºåÈáùÂ∞çConicConformalÊäïÂΩ±ÈôêÂà∂ÁØÑÂúç
       */
      const generateGridLines = (projectionType = 'default') => {
        const gridLines = [];

        // Ê†πÊìöÊäïÂΩ±È°ûÂûãË®≠ÂÆö‰∏çÂêåÁöÑÁ∂ìÁ∑ØÂ∫¶ÁØÑÂúç
        let latMin, latMax, lonMin, lonMax;

        if (projectionType === 'ConicConformal') {
          // ConicConformal ÊäïÂΩ±ÔºöÂæûÂåóÁ∑Ø90Â∫¶Âà∞ÂçóÁ∑Ø60Â∫¶Ôºå‰∏çÈ°ØÁ§∫ÂçóÊ•µ
          latMin = -60;
          latMax = 90;
          lonMin = -180;
          lonMax = 180;
        } else {
          // ÂÖ∂‰ªñÊäïÂΩ±Ôºö‰ΩøÁî®ÂÆåÊï¥ÁØÑÂúç
          latMin = -90;
          latMax = 90;
          lonMin = -180;
          lonMax = 180;
        }

        // ÁîüÊàêÁ∑ØÁ∑ö (ÊØè30Â∫¶‰∏ÄÊ¢ù)
        for (let lat = latMin; lat <= latMax; lat += 30) {
          // Ë∑≥ÈÅéÊ•µÈªûÔºàÂÆÉÂÄëÊòØÈªûËÄåÈùûÁ∑öÔºâ
          if (lat === -90 || lat === 90) continue;

          const line = {
            type: 'Feature',
            geometry: {
              type: 'LineString',
              coordinates: [],
            },
          };

          // ÊØèÊ¢ùÁ∑ØÁ∑öÁî±Â§öÂÄãÈªûÁµÑÊàê
          for (let lon = lonMin; lon <= lonMax; lon += 1) {
            line.geometry.coordinates.push([lon, lat]);
          }

          gridLines.push(line);
        }

        // ÁîüÊàêÁ∂ìÁ∑ö (ÊØè30Â∫¶‰∏ÄÊ¢ù)
        for (let lon = lonMin; lon <= lonMax - 30; lon += 30) {
          const line = {
            type: 'Feature',
            geometry: {
              type: 'LineString',
              coordinates: [],
            },
          };

          // ÊØèÊ¢ùÁ∂ìÁ∑öÁî±Â§öÂÄãÈªûÁµÑÊàê
          for (let lat = latMin; lat <= latMax; lat += 1) {
            line.geometry.coordinates.push([lon, lat]);
          }

          gridLines.push(line);
        }

        return {
          type: 'FeatureCollection',
          features: gridLines,
        };
      };

      const getFeaturesForView = () => {
        const data = worldData.value;
        if (!data?.features) return [];

        if (currentViewMode.value === 'taiwan') {
          return data.features.filter((feature) => {
            const countryName =
              feature.properties?.name ||
              feature.properties?.ADMIN ||
              feature.properties?.NAME ||
              feature.properties?.adm0_a3 ||
              feature.properties?.ADM0_A3;
            return dataStore.isHomeCountry(countryName);
          });
        }

        return data.features;
      };

      const getFitTarget = () => ({ type: 'Sphere' });

      const featureKey = (feature, index) =>
        feature.properties?.ADM0_A3 ||
        feature.properties?.adm0_a3 ||
        feature.properties?.ISO_A3 ||
        feature.properties?.iso_a3 ||
        feature.properties?.NAME ||
        feature.properties?.ADMIN ||
        feature.properties?.name ||
        feature.id ||
        `idx-${index}`;

      const renderSphereBorder = () => {
        if (!gBorder) return;
        gBorder.selectAll('path.sphere').remove();

        gBorder
          .append('path')
          .datum({ type: 'Sphere' })
          .attr('class', 'sphere')
          .attr('d', path)
          .attr('fill', 'none')
          .attr('stroke', '#999999')
          .attr('stroke-width', 2);
      };

      const renderCountries = () => {
        if (!g || !worldData.value) return;
        const features = getFeaturesForView();
        const selection = g.selectAll('path.country').data(features, featureKey);
        selection.exit().remove();
        const merged = selection
          .enter()
          .append('path')
          .attr('class', 'country')
          .merge(selection);

        merged
          .attr('d', path)
          .attr('fill', (d) => {
            const countryName =
              d.properties.name || d.properties.ADMIN || d.properties.NAME;
            if (dataStore.isHomeCountry(countryName)) return '#ff0000';
            return '#999999';
          })
          .attr('stroke', 'none');
      };

      const renderTaiwanGuides = () => {
        if (!g || !projection) return;

        const createMeridian = (lon, id) => {
          const coordinates = [];
          for (let lat = -90; lat <= 90; lat += 1) {
            coordinates.push([lon, lat]);
          }
          return { id, type: 'Feature', geometry: { type: 'LineString', coordinates } };
        };

        const createParallel = (lat, id) => {
          const coordinates = [];
          for (let lon = -180; lon <= 180; lon += 1) {
            coordinates.push([lon, lat]);
          }
          return { id, type: 'Feature', geometry: { type: 'LineString', coordinates } };
        };

        const guidesData = [
          createMeridian(TAIWAN_CENTER[0], 'guide-longitude-taiwan-east'),
          createMeridian(TAIWAN_CENTER[0] - 180, 'guide-longitude-taiwan-west'),
          createParallel(TAIWAN_CENTER[1], 'guide-latitude-taiwan'),
          createMeridian(0, 'guide-longitude-0-east'),
          createMeridian(-180, 'guide-longitude-0-west'),
          createParallel(0, 'guide-equator'),
        ];

        const selection = g
          .selectAll('path.taiwan-guide')
          .data(guidesData, (d) => d.id);

        selection.exit().remove();

        const merged = selection
          .enter()
          .append('path')
          .attr('class', 'taiwan-guide')
          .merge(selection);

        merged
          .attr('d', (d) => path(d))
          .attr('fill', 'none')
          .attr('stroke', (d) =>
            d.id === 'guide-longitude-0-east' ||
            d.id === 'guide-longitude-0-west' ||
            d.id === 'guide-equator'
              ? '#44b046'
              : '#366cb4'
          )
          .attr('stroke-width', 4)
          .attr('opacity', 0.9)
          .raise();
      };


const renderGridLines = () => {
  if (!g) return;
  const gridData = generateGridLines(currentProjectionType.value);
  const selection = g
    .selectAll('path.grid-line')
    .data(gridData.features, (d, i) => d?.id || `grid-${i}`);
  selection.exit().remove();
  const merged = selection
    .enter()
    .append('path')
    .attr('class', 'grid-line')
    .merge(selection);

  merged
    .attr('d', path)
    .attr('fill', 'none')
    .attr('stroke', '#999999')
    .attr('stroke-width', currentViewMode.value === 'taiwan' ? 0.5 : 1)
    .attr('opacity', currentViewMode.value === 'taiwan' ? 0.6 : 0.8);
};

      /**
       * üé® Áπ™Ë£Ω‰∏ñÁïåÂú∞Âúñ
       */
      const drawWorldMap = async () => {
        if (!g || !worldData.value) {
          console.error('[MapTab] ÁÑ°Ê≥ïÁπ™Ë£ΩÂú∞Âúñ: g=', !!g, 'worldData=', !!worldData.value);
          return;
        }

        try {
          const features = getFeaturesForView();
          console.log('[MapTab] ÈñãÂßãÁπ™Ë£ΩÂú∞ÂúñÔºåÁâπÂæµÊï∏Èáè:', features.length);

          renderSphereBorder();
          renderCountries();
          renderGridLines();
          renderTaiwanGuides();

          console.log('[MapTab] Âú∞ÂúñÁπ™Ë£ΩÂÆåÊàêÔºåÊ®°Âºè:', currentViewMode.value);
        } catch (error) {
          console.error('[MapTab] ‰∏ñÁïåÂú∞ÂúñÁπ™Ë£ΩÂ§±Êïó:', error);
        }
      };

      // addCityMarkers ÂáΩÊï∏Â∑≤ÁßªÈô§ - ‰∏çÂÜçÈúÄË¶ÅÂüéÂ∏ÇÊ®ôË®ò

      /**
       * üåç Â∞éËà™Âà∞ÊåáÂÆö‰ΩçÁΩÆ
       * ‰øùÁïôÊ≠§ÂáΩÊï∏‰ª•Á∂≠ÊåÅÂÖºÂÆπÊÄß
       */
      const navigateToLocation = () => {
        // Âú∞Âúñ‰∏≠ÂøÉÈªûÂ∑≤Âõ∫ÂÆöÂú®Á∂ìÁ∑ØÂ∫¶ 0,0
        console.log('[MapTab] Âú∞Âúñ‰∏≠ÂøÉÈªûÂ∑≤Âõ∫ÂÆö');
      };

      /**
       * üìè Âà∑Êñ∞Âú∞ÂúñÂ∞∫ÂØ∏
       * Áï∂ÂÆπÂô®Â§ßÂ∞èÊîπËÆäÊôÇÈáçÊñ∞Ë®àÁÆóÂú∞ÂúñÂ∞∫ÂØ∏
       */
      const invalidateSize = () => {
        if (!svg || !mapContainer.value) return;

        const rect = mapContainer.value.getBoundingClientRect();
        const width = rect.width;
        const height = rect.height;

        svg.attr('width', width).attr('height', height);

        projection = createProjection(currentProjectionType.value, width, height);
        path = d3.geoPath().projection(projection);

        // Êõ¥Êñ∞Ë£ÅÂâ™Ë∑ØÂæë
        if (clipPathId) {
          svg.select(`#${clipPathId} path`).datum({ type: 'Sphere' }).attr('d', path);
        }

        renderSphereBorder();
        renderCountries();
        renderGridLines();
        renderTaiwanGuides();

        console.log('[MapTab] Âú∞ÂúñÂ∞∫ÂØ∏Êõ¥Êñ∞ÂÆåÊàêÔºåÊ®°Âºè:', currentViewMode.value);
      };

      /**
       * üöÄ ÂàùÂßãÂåñÂú∞Âúñ
       * ÂâµÂª∫Âú∞Âúñ‰∏¶ËºâÂÖ•ÂàùÂßãÊï∏Êìö
       */
      const initMap = async () => {
        let attempts = 0;
        const maxAttempts = 20;

        // ÂÖàËºâÂÖ•‰∏ñÁïåÂú∞ÂúñÊï∏Êìö
        const loaded = await loadWorldData();
        if (!loaded) {
          console.error('[MapTab] ÁÑ°Ê≥ïËºâÂÖ•‰∏ñÁïåÂú∞ÂúñÊï∏Êìö');
          return;
        }

        const tryCreateMap = async () => {
          if (attempts >= maxAttempts) {
            console.error('[MapTab] Âú∞ÂúñÂàùÂßãÂåñÂ§±ÊïóÔºåÂ∑≤ÈÅîÂà∞ÊúÄÂ§ßÂòóË©¶Ê¨°Êï∏');
            return;
          }

          attempts++;
          console.log(`[MapTab] ÂòóË©¶ÂâµÂª∫Âú∞Âúñ (${attempts}/${maxAttempts})`);

          if (createMap()) {
            console.log('[MapTab] Âú∞ÂúñÂâµÂª∫ÊàêÂäüÔºåÈñãÂßãÁπ™Ë£Ω‰∏ñÁïåÂú∞Âúñ');
            await drawWorldMap();
          } else {
            console.log('[MapTab] Âú∞ÂúñÂâµÂª∫Â§±ÊïóÔºå100ms ÂæåÈáçË©¶');
            setTimeout(tryCreateMap, 100);
          }
        };

        tryCreateMap();
      };

      // üìè Ë®≠ÁΩÆ ResizeObserver Áõ£ËÅΩÂÆπÂô®Â§ßÂ∞èËÆäÂåñ
      let resizeObserver = null;
      let resizeTimeout = null;

      const setupResizeObserver = () => {
        if (!mapContainer.value || !window.ResizeObserver) return;

        resizeObserver = new ResizeObserver(() => {
          if (resizeTimeout) {
            clearTimeout(resizeTimeout);
          }

          resizeTimeout = setTimeout(() => {
            console.log('üîÑ ÂÆπÂô®Â§ßÂ∞èËÆäÂåñÔºåÂà∑Êñ∞Âú∞Âúñ');
            invalidateSize();
          }, 200);
        });

        resizeObserver.observe(mapContainer.value);
        console.log('‚úÖ ResizeObserver Â∑≤Ë®≠ÁΩÆ');
      };

      // üßπ ÁîüÂëΩÈÄ±ÊúüÔºöÁµÑ‰ª∂ÊéõËºâ
      onMounted(() => {
        nextTick(() => {
          initMap();
          setupResizeObserver();
        });
      });

      // üßπ ÁîüÂëΩÈÄ±ÊúüÔºöÁµÑ‰ª∂Âç∏Ëºâ
      onUnmounted(() => {
        if (resizeTimeout) {
          clearTimeout(resizeTimeout);
        }

        if (resizeObserver) {
          resizeObserver.disconnect();
        }

        if (svg) {
          svg.remove();
          svg = null;
        }

        projection = null;
        path = null;
        zoom = null;
        g = null;
        gBorder = null;
        clipPathId = null;
        isMapReady.value = false;
      });

      // üëÄ Áõ£ËÅΩÂô®ÔºöÁõ£ËÅΩË≥áÊñôÂ≠òÂÑ≤‰∏≠ÁöÑÂúñÂ±§ËÆäÂåñ
      watch(
        () => dataStore.layers,
        () => {
          if (isMapReady.value) {
            // Ë∑ùÈõ¢ÂúìÂúàÂäüËÉΩÂ∑≤ÁßªÈô§
          }
        },
        { deep: true }
      );

      // üëÄ Áõ£ËÅΩÂô®ÔºöÁõ£ËÅΩÁï∂ÂâçÊäïÂΩ±È°ûÂûãËÆäÂåñ
      watch(
        () => props.currentProjection,
        (newProjection) => {
          if (isMapReady.value && newProjection) {
            // currentProjection ÊòØ layerNameÔºåÈúÄË¶ÅÊâæÂà∞Â∞çÊáâÁöÑÊäïÂΩ±ÂúñÂ±§
            const allLayers = dataStore.getAllLayers();
            const layer = allLayers.find((l) => l.layerName === newProjection);
            if (layer) {
              changeProjection(layer.type, layer.scale);
            }
          }
        }
      );

      // üì§ ËøîÂõûÁµÑ‰ª∂ÂÖ¨ÈñãÁöÑÂ±¨ÊÄßÂíåÊñπÊ≥ï
      return {
        mapContainer,
        mapContainerId,
        isAnyLayerVisible,
        invalidateSize,
        navigateToLocation,
        changeProjection,
        setMapCenter,
        setViewMode,
        // ConicConformal Áõ∏Èóú
        conicConformalScale,
        setConicConformalScale,
      };
    },
  };
</script>

<template>
  <!-- üó∫Ô∏è Âú∞Âúñ‰∏ªÂÆπÂô® -->
  <div id="map-container" class="h-100 w-100 position-relative bg-transparent z-0">
    <!-- üó∫Ô∏è D3.js Âú∞ÂúñÂÆπÂô® -->
    <div :id="mapContainerId" ref="mapContainer" class="h-100 w-100"></div>
  </div>
</template>

<style scoped>
  @import '../assets/css/common.css';

  #map-container {
    overflow: hidden;
  }

  /* Ë∑ùÈõ¢ÂúìÂúàÁèæÂú®‰ΩøÁî® D3.js Áπ™Ë£ΩÔºå‰∏çÈúÄË¶Å CSS Ê®£Âºè */

  :deep(.country) {
    transition: fill 0.2s ease;
  }

  :deep(.country:hover) {
    fill: #c0c0c0;
  }

  :deep(.city-marker) {
    transition: r 0.2s ease;
  }

  :deep(.city-marker:hover) {
    r: 6;
  }

  :deep(.grid-line) {
    transition: opacity 0.3s ease;
  }

  :deep(.grid-line:hover) {
    opacity: 1 !important;
    stroke-width: 2 !important;
    stroke: #333333 !important;
  }
</style>
